import React, { useState, useEffect } from "react";
import Gif from "../Gif";
import Form from "../Form";
import Question from "../Question";
import Progress from "../Progress";
import useWindowSize from "react-use/lib/useWindowSize";
import Confetti from "react-confetti";
import Winner from "../Winner";

function Page() {
  const [chickenTitle, setChickenTitle] = useState(" ");
  const [chickenData, setChickenData] = useState("");
  const [chickenCounter, setChickenCounter] = useState(0);
  const [chickenState, setChickenState] = useState(0);

  const validateForm = (correct, cStatus) => {
    if (correct == "") {
      setChickenTitle("random");
      if (chickenCounter < 5) {
        setChickenState(chickenState + 1);
      }
    } else {
      setChickenTitle(correct);
      if (chickenCounter < 5) {
        setChickenState(chickenState + 1);
      }
    }
    if (cStatus == true) {
      setChickenCounter(chickenCounter + 1);
    } else {
      setChickenCounter(0);
    }
    console.log("App - validateFrom() - formies", chickenTitle);
  };

  const resetChicken = () => {
    setChickenCounter(0);
    setChickenTitle("");
    setChickenState(0);
  };

  useEffect(() => {
    let chickenUrl = `https://opentdb.com/api.php?amount=1&difficulty=easy&type=multiple&encode=base64&category=9`;

    const makeApiCall = () => {
      fetch(chickenUrl)
        .then((res) => res.json())
        .then((data) => {
          console.log(
            "chickenData",
            data.results[0],
            "counter is ",
            chickenCounter
          );
          setChickenData(data.results[0]);
        });
    };
    makeApiCall();
  }, [chickenState]);
  const { width, height } = useWindowSize();

  if (chickenCounter == 5) {
    return (
      <div>
        <Confetti width={width} height={height} />
        <div className="chickenHeader">
          <h1>ğŸ” Big Chicken</h1>
          <h4>Answer questions to grow your chicken</h4>
          <Progress counter={chickenCounter} />
        </div>
        <Winner title={chickenTitle} reset={resetChicken} />
      </div>
    );
  } else {
    return (
      <div>
        <div className="chickenHeader">
          <h1>ğŸ” Big Chicken</h1>
          <h4>Answer questions to grow your chicken</h4>
          <Progress counter={chickenCounter} />
        </div>
        <Question
          question={chickenData}
          incorrect={chickenData.incorrect_answers}
          valForm={validateForm}
        />
        <div className="chickenTitle">
          <p>
            <i>{chickenTitle}</i>
          </p>
        </div>
      </div>
    );
  }
}

export default Page;
